import React from 'react';
import autobind from 'class-autobind';
import PersistedQueryEditor from './Query-persisted';
import PersistedResultEditor from './Result-persisted';
import '../css/style-persisted.css';

export default class Persisted extends React.PureComponent {
  constructor () {
    super(...arguments);
    autobind(this);
  }

  handleInfoClick () {}

  render () {
    const {
      fetchGraphql,
      handleInfoClick,
      handleOnChangeQuery,
      handleOnChangeRequest,
      handlePrettifyClick,
      handleResetClick,
      handleSaveClick,
      selectedPersisted
    } = this.props;

    return (
      <div className="Persisted">
        <div className="Persisted-toolbar">
          <ul>
            <li className="Persisted-toolbar-item">
              <button onClick={fetchGraphql}>Fetch</button>
            </li>
            <li className="Persisted-toolbar-item">
              <button onClick={handlePrettifyClick}>Prettify</button>
            </li>
            <li className="Persisted-toolbar-item">
              <button onClick={handleSaveClick}>Save</button>
            </li>
            <li className="Persisted-toolbar-item">
              <button onClick={handleResetClick}>Reset</button>
            </li>
            <li className="Persisted-toolbar-item">
              <button onClick={handleInfoClick}>Info</button>
            </li>
          </ul>
        </div>
        <PersistedQueryEditor
          selectedPersisted={selectedPersisted}
          handleOnChange={handleOnChangeQuery}
        />
        <PersistedResultEditor
          selectedPersisted={selectedPersisted}
          handleOnChange={handleOnChangeRequest}
        />
      </div>
    );
  }
}



